import React, { FC } from 'react';
import {  Grid, Typography } from '@material-ui/core/';
import clsx from 'clsx';
import Button from '@material-ui/core/Button';
import Popover from '@material-ui/core/Popover';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import FormControl from '@material-ui/core/FormControl';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload } from '@fortawesome/free-solid-svg-icons';
import { makeStyles } from '@material-ui/core/styles';

import { IReportPopover } from './IReportPopover';

const useStyles = makeStyles(theme => ({
	dialogAction: {
		marginBottom: theme.spacing(1),
		justifyContent: 'center'
	},
}));

export const ReportPopover: FC<IReportPopover.IProps> = props => {
	const {open, anchorEl, exportData, exportFormat, onClose, onChangeExportData, onChangeExportFormat, generateReport} = props;
	const classes = useStyles();

	return (
		<Popover
			open={open}
			anchorEl={anchorEl}
			onClose={onClose}
			anchorOrigin={{
			    vertical: 'bottom',
			    horizontal: 'right',
			}}
			transformOrigin={{
				vertical: 'top',
				horizontal: 'right',
			}}
		>
			<Grid
				container
			>
				<Grid item>
					<DialogContent>
						<Typography variant="body1" style={{ fontWeight: 'bold' }}>
							Choose Data
						</Typography>
						
						<FormControl component="fieldset">
							<RadioGroup name="report_data">
								<FormControlLabel
									control={ 
												<Radio 
													checked={exportData === 'all_rows'}
													name="all_rows"
													onClick={(e) => {
														e.stopPropagation();
														onChangeExportData("all_rows");
													}}
												/>
											}
									label="Include all rows"
								/>
								<FormControlLabel
									control={ 
												<Radio 
													checked={exportData === 'filtered_rows'}
													name="filtered_rows"
													onClick={(e) => {
														e.stopPropagation();
														onChangeExportData("filtered_rows");
													}}
												/>
											}
									label="Include only filtered rows"
								/>
							</RadioGroup>
						</FormControl>

						<Typography variant="body1" style={{ fontWeight: 'bold' }}>
							Choose Format
						</Typography>

						<FormControl component="fieldset">
							<RadioGroup name="report_format">
								<FormControlLabel
									control={ 
												<Radio 
													checked={exportFormat === 'pdf'}
													name="pdf"
													onClick={(e) => {
														e.stopPropagation();
														onChangeExportFormat("pdf");
													}}
												/>
											}
									label="PDF"
								/>
								<FormControlLabel
									control={ 
												<Radio 
													checked={exportFormat === 'xls'}
													name="xls"
													onClick={(e) => {
														e.stopPropagation();
														onChangeExportFormat("xls");
													}}
												/>
											}
									label="XLS"
								/>
								<FormControlLabel
									control={ 
												<Radio 
													checked={exportFormat === 'xml'}
													name="xml"
													onClick={(e) => {
														e.stopPropagation();
														onChangeExportFormat("xml");
													}}
												/>
											}
									label="XML"
								/>
							</RadioGroup>
						</FormControl>
					</DialogContent>
					<DialogActions className={clsx(classes.dialogAction)}>
						<Button
							variant="contained"
							color="primary"
							onClick={generateReport}
						>
							Download Report&nbsp;&nbsp;<FontAwesomeIcon icon={faDownload} />
						</Button>
					</DialogActions>
				</Grid>
			</Grid>
		</Popover>
	);
};
