const textFieldStyles = makeStyles({
	root: {
		'& > div': {
			backgroundColor: 'transparent',
		}, 
		'& .Mui-disabled': {
			color: "#272E4C"
		}	
	}
});

const datePickerStyles = makeStyles({
	keyboardDatePicker: {
		// marginTop: '10px',
		// marginBottom: '20px',
		width: '100%',
	},
});
const Select = ({ items, ...restProps  }: any) => {
	return (
		<MaterialUI.Select
			variant='outlined'
			style={{ width: '100%' }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value === selected;
					});
					if (value) {
						return value.label;
					} else {
						return '——';
					}
				}
				return '——';
			}}
			{...restProps}
		>
			{items.map((item: any, i: number) => (
				<MaterialUI.MenuItem key={i} value={item.value}>
					{item.label}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};
const StatusItems = [
	{ label: '', value: '' },
	{ label: 'Active', value: 'Active' },
	{ label: 'Approved', value: 'Approved' },
	{ label: 'Disapproved', value: 'Disapproved' },
	{ label: 'For Approval', value: 'For Approval' },
	{ label: 'Signed', value: 'Signed' },
	{ label: 'For Revision', value: 'For Revision' },
	{ label: 'Submitted', value: 'Submitted' },
	{ label: 'Resubmitted', value: 'Resubmitted' },
];

const ProposalsFilterCell = (props: any) => {
  	const { column, filter, onFilter, filteringEnabled, classes } = props;
	const datePickerClasses = datePickerStyles();
	if (filteringEnabled) {
		if (column.name === 'status') {
			const items = StatusItems;

			return 	<Select
						onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
						name={'status'}
						items={items}
						id={'status'}
						value={filter ? filter.value : ''}
					/>;
		} else if (column.name === 'effectivity_date' 
					|| column.name === 'date_created' 
					|| column.name === 'date_modified' ) {
			return (
				<MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
					<KeyboardDatePicker
						variant="dialog"
						format="MMMM DD, YYYY"
						inputVariant="outlined"
						className={datePickerClasses.keyboardDatePicker}
						value={filter ? filter.value : ''}
						invalidDateMessage=""
						onChange={date => {console.log(date); onFilter(date ? { value: date } : null)}}
					/>
				</MaterialUI.TableCell>
			);
		} else {
			return (
				<MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
					<MaterialUI.OutlinedInput
						classes={classes}
						style={{ width: '100%' }}
						value={filter ? filter.value : ''}
						labelWidth={0}
						onChange={e => {console.log('filter changed');onFilter(e.target.value ? { value: e.target.value } : null)}}
						placeholder=""
					/>
				</MaterialUI.TableCell>
			);
		}
	} 
	return null;
}
